import React, { Component } from "react";
import {
  Card,
  CardText,
  CardBody,
  CardTitle
} from "reactstrap";
import { FaRegTrashAlt, FaRegEdit } from "react-icons/fa";
import { DelPost, EditPost } from "../../../redux/actions/PostCommentAction";
import ModalPost from "../../ModalPost";
import { connect } from "react-redux";

class ListItem extends Component {
  constructor(props) {
    super(props);
    this.state={
      modal: false,
      
    }
    this.toggle = this.toggle.bind(this);
  }

  onHandleClickToEdit = () => {
    <ModalPost isopen={this.state.modal}/>
    const { id } = this.props.list_massage;
    const { EditPost } = this.props;
    EditPost(id);
  };
  onHandleClickToDel = () => {
    const { id } = this.props.list_massage;
    const { DelPost } = this.props;
    DelPost(id);
  };
  render() {
    const { id, data_field, id_users } = this.props.list_massage;
    return (
      <div>
        <Card className="blockpost">
          <div style={{ textAlign: "right" }}>
            <a name={id} onClick={() => this.onHandleClickToEdit()}>
              <FaRegEdit />
            </a>
            <a name={id} onClick={() => this.onHandleClickToDel()}>
              <FaRegTrashAlt />
            </a>
          </div>
          <CardBody>
            <CardTitle>{id_users}</CardTitle>
            <CardText>
              <span>{data_field}</span>
              <span>{}</span>
            </CardText>
          </CardBody>
        </Card>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {};
};

export default connect(
  mapStateToProps,
  { DelPost, EditPost }
)(ListItem);
